import {
	mapState
} from "vuex"
const audioContext = uni.createInnerAudioContext();
audioContext.autoplay = false;
let currentTime ="", time = 0  
export default {
	data() {
		return {
			optList: [{
				'id': 0,
				'icon': 'cuIcon-like'
			}, {
				'id': 1,
				'icon': 'cuIcon-down'
			}, {
				'id': 2,
				'icon': 'cuIcon-notice'
			}, {
				'id': 3,
				'icon': 'cuIcon-message'
			}, {
				'id': 4,
				'icon': 'cuIcon-moreandroid'
			}, ],
			duration: '100',
			currentTime: '0',
			chDuration: ['0', '00'],
			chCurrentTime: ['0', '00'],
			ifPlay: false,
			muiscName:""
		}
	},
	onLoad(options) {
		let {name,id} = options
		// console.log("+++++++++++++++++",name,id)
		this.muiscName = name
		// let id = options.id
		this.$store.dispatch("getMusic", id)
		this.$store.dispatch("musicFace", id)
		console.log("this.muisc",this.muisc)
		
	},
	computed: {
		...mapState(["muisc", "muiscface"]),
		
	},
	methods: {
		goBack() {
			history.back();
		},
		playMusic() {
			audioContext.src = this.muisc;
			var duration = audioContext.duration;
			currentTime = audioContext.currentTime;
			this.currentTime = currentTime;
			this.duration = duration;
			
			this.chDuration[0] = Math.floor(Math.floor(duration) / 60);
			this.chDuration[1] = Math.floor(duration) % 60;
			
			setInterval(()=>{
				time = time+1
				console.log(time)
				// this.chCurrentTime[0] = Math.floor(Math.floor(currentTime) / 60);
				// this.chCurrentTime[1] = Math.floor(currentTime) % 60;
			},1000)
			
			if (this.ifPlay) {
				this.ifPlay = false;
				audioContext.pause();
			} else {
				this.ifPlay = true;
				audioContext.play();
			}
		},
		changeProgress(e) {
			console.log("asfasg:+++++++++",e)
			audioContext.seek(e.detail.value);
			this.playMusic();
			// this.ifPlay = false;
		},
	}
}
